<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process15"></a>

<p><br></p>
<p><span class="rvts17">不死族（Undead）:</span></p>
<p><img width="316" height="752" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber53.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">亡灵族是瑞维伦的一种令人憎恶的种族，被认为是自然秩序的可憎之物。那些想要在生活中行走的人必须掩饰自己，否则就会引发暴力。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族天赋：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="240" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图</span><span class="rvts45">标</span></p>
</td>
<td width="102" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="121" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="688" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="240" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber54.jpg"></p>
</td>
<td width="102" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Undead</span></p>
</td>
<td width="121" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">亡灵</span></p>
</td>
<td width="688" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">可以从毒素中得到治疗，但会从常规治疗中受到伤害</span></p>
</td>
</tr>
<tr valign="top">
<td width="240" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">随种族特征</span></p>
</td>
<td width="102" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">varified</span></p>
</td>
<td width="121" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">种族特性</span></p>
</td>
<td width="688" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">根据种族不同，具备不同的天赋特性。</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts47"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="114" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图</span><span class="rvts45">标</span></p>
</td>
<td width="242" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="114" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="681" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="114" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber55.jpg"></p>
</td>
<td width="242" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Play Dead</span></p>
</td>
<td width="114" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">装死</span></p>
</td>
<td width="681" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">愚蠢的敌人会认为你死掉了，技能冷却时间会被暂停。</span>
<br><span class="rvts21">玩家此时使用任何技能都会打断该状态。</span></p>
</td>
</tr>
<tr valign="top">
<td width="114" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber56.jpg"></p>
</td>
<td width="242" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Dome of Protection</span></p>
</td>
<td width="114" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">守护圆罩</span></p>
</td>
<td width="681" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">提升护罩内队友的物理和法术防御值。</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts43"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts21">不死族可以是另外</span><span class="rvts20">4</span><span class="rvts21">大种族中的任意一种不死版本，不死族可以在中毒效果下获得生命恢复（抗毒</span><span class="rvts20">+100%</span><span class="rvts21">）。不死族可以不使用</span><span class="rvts20"></span><span class="rvts21">撬锁器</span><span class="rvts20"></span><span class="rvts21">而直接进行</span><span class="rvts20"></span><span class="rvts21">撬锁</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts21">另外，不死族角色无法流血，可以装死脱战或降低仇恨。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



